.input_lable {
	display: block;
	margin: 0;
	margin-bottom: 50px;
	width: 100%;
	height: 56px;
 }
.input {
	background: #FFFFFF;
	box-shadow: 0px 16px 32px rgba(202, 203, 210, 0.25);
	border-radius: 8px;
	border: none;
	outline: none;
	width: 100%;
	height: 64px;
	padding: 18px 20px;
	margin: 8px 0;
	box-sizing: border-box;
	font-weight: 400;
		font-size: 16px;
		line-height: 28px;
		color: #414042;
}
.input::placeholder{
	color: #a9a9a9;
	font-weight: 400;
		font-size: 16px;
		line-height: 28px;
}